import { ThemeEditor } from "@/components";
import Layout from "@/components/Layout";
import { FeatureSection, HelpSection, HeroSection } from "@/components/home";
import {
  Box,
  Container,
  useBreakpointValue,
  VStack,
  Button,
  Flex,
  Heading,
  Text,
  Collapse,
  Icon,
} from "@chakra-ui/react";
import { HeadFC, PageProps } from "gatsby";
import * as React from "react";
import { ChevronDown, ChevronUp } from "lucide-react";

const IndexPage: React.FC<PageProps> = () => {
  const isMobile = useBreakpointValue({ base: true, md: false });
  const [showFeatures, setShowFeatures] = React.useState(!isMobile ? true : false);
  const [showHelp, setShowHelp] = React.useState(!isMobile ? true : false);

  // For smooth scrolling to sections
  const scrollToSection = (id: string) => {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: "smooth", block: "start" });
    }
  };

  return (
    <Layout>
      <Box>
        {/* Hero section takes full width */}
        <Container maxW="8xl" px={{ base: 4, md: 6 }}>
          <HeroSection />
        </Container>

        {/* Theme Editor Section */}
        <Box py={{ base: 10, md: 16 }} id="theme-editor-section">
          <Container maxW="7xl" px={{ base: 4, md: 6 }}>
            <VStack spacing={8} align="stretch">
              <Heading
                fontSize={{ base: "2xl", md: "3xl" }}
                textAlign="center"
                color="secondary.500"
              >
                Design Your Perfect Theme
              </Heading>
              <ThemeEditor />
            </VStack>
          </Container>
        </Box>

        {/* Features Section with collapsible behavior on mobile */}
        <Box py={{ base: 8, md: 16 }} id="features-section">
          <Container maxW="7xl" px={{ base: 4, md: 6 }}>
            <VStack spacing={{ base: 6, md: 12 }} align="stretch">
              <Flex 
                direction="column" 
                align="center"
                onClick={() => isMobile && setShowFeatures(!showFeatures)}
                cursor={isMobile ? "pointer" : "default"}
                position="relative"
              >
                <Heading
                  fontSize={{ base: "2xl", md: "3xl" }}
                  textAlign="center"
                  color="secondary.500"
                  mb={isMobile ? 0 : 2}
                >
                  Powerful Features
                </Heading>
                {isMobile && (
                  <Flex align="center" mt={2}>
                    <Text fontSize="sm" color="gray.500">
                      {showFeatures ? "Hide features" : "Show features"}
                    </Text>
                    <Icon as={showFeatures ? ChevronUp : ChevronDown} ml={1} color="gray.500" />
                  </Flex>
                )}
              </Flex>
              
              <Collapse in={showFeatures} animateOpacity>
                <FeatureSection />
              </Collapse>
            </VStack>
          </Container>
        </Box>

        {/* Help Section with collapsible behavior on mobile */}
        <Box py={{ base: 8, md: 16 }} id="help-section">
          <Container maxW="7xl" px={{ base: 4, md: 6 }}>
            <VStack spacing={{ base: 6, md: 8 }} align="stretch">
              <Flex 
                direction="column" 
                align="center"
                onClick={() => isMobile && setShowHelp(!showHelp)}
                cursor={isMobile ? "pointer" : "default"}
                position="relative"
              >
                <Heading
                  fontSize={{ base: "2xl", md: "3xl" }}
                  textAlign="center"
                  color="secondary.500"
                  mb={isMobile ? 0 : 2}
                >
                  Guide for Designers
                </Heading>
                {isMobile && (
                  <Flex align="center" mt={2}>
                    <Text fontSize="sm" color="gray.500">
                      {showHelp ? "Hide guide" : "Show guide"}
                    </Text>
                    <Icon as={showHelp ? ChevronUp : ChevronDown} ml={1} color="gray.500" />
                  </Flex>
                )}
              </Flex>
              
              <Collapse in={showHelp} animateOpacity>
                <HelpSection />
              </Collapse>
            </VStack>
          </Container>
        </Box>

        {/* Quick Navigation on Mobile */}
        {isMobile && (
          <Box 
            position="fixed" 
            bottom={0} 
            left={0} 
            right={0} 
            bg="rgba(255,255,255,0.9)" 
            backdropFilter="blur(8px)"
            boxShadow="0 -4px 6px -1px rgba(0, 0, 0, 0.1)"
            zIndex={10}
            py={2}
            _dark={{
              bg: "rgba(26,32,44,0.9)"
            }}
          >
            <Flex justify="space-around">
              <Button 
                variant="ghost" 
                size="sm" 
                onClick={() => scrollToSection("theme-editor-section")}
                fontWeight="medium"
              >
                Editor
              </Button>
              <Button 
                variant="ghost" 
                size="sm" 
                onClick={() => {
                  setShowFeatures(true);
                  scrollToSection("features-section");
                }}
                fontWeight="medium"
              >
                Features
              </Button>
              <Button 
                variant="ghost" 
                size="sm" 
                onClick={() => {
                  setShowHelp(true);
                  scrollToSection("help-section");
                }}
                fontWeight="medium"
              >
                Guide
              </Button>
            </Flex>
          </Box>
        )}
      </Box>
    </Layout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home | ChakraUI Themes Editor</title>;
